  box-sizing: border-box;
  width: 100%;
  min-width: none;
  max-width: none;
  margin: 0;
  padding: 0 1.5rem;
}

.row {
  box-sizing: border-box;
  width: 100%;
  margin-left: 0;
  margin-right: 0;
}

.col {
  box-sizing: border-box;
  width: 100%;
  float: none;
  padding-left: 0;
  padding-right: 0;
}

