==============================================================================*/
.oc-wrap {
  height: 100%;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  transition: top linear 0.2s, bottom linear 0.2s;
}

.oc-content,
.oc-inner,
.oc-aside {
  height: 100%;
}

.oc-wrap {
  position: relative;
  overflow: hidden;
}

.oc-content {
  position: relative;
  left: 0;
  z-index: 5;
  transition: transform 0.5s;
}

.oc-inner {
  position: relative;
  overflow-y: auto;
}

.oc-aside {
  position: fixed;
  top: 0;
  z-index: 10;
  visibility: hidden;
  width: 280px;
  overflow-y: auto;
  transition: all 0.5s;
  background: #fff;
}

.oc-content:after {
  position: absolute;
  top: 0;
  right: 0;
  width: 0;
  height: 0;
  background: rgba(0, 0, 0, 0.2);
  content: '';
  opacity: 0;
  transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
}

.oc-active > .oc-content:after {
  width: 100%;
  height: 100%;
  opacity: 1;
  transition: opacity 0.5s;
}

.oc-aside:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.2);
  content: '';
  opacity: 1;
  transition: opacity 0.5s;
}

.oc-active > .oc-aside:after {
  width: 0;
  height: 0;
  opacity: 0;
  transition: opacity 0.5s, width 0.1s 0.5s, height 0.1s 0.5s;
}

.oc-aside-left.oc-aside {
  left: 0;
  visibility: visible;
  transform: translate3d(-100%, 0, 0);
}

.oc-aside-left.oc-active .oc-aside-left.oc-aside {
  visibility: visible;
  transform: translate3d(0, 0, 0);
}

.oc-aside-left.oc-aside:after {
  display: none;
}

